<template>

  <div class="write-blog">

    <!-- 二级标题开始 -->
    <div class="title_bg">
      <div class="title_box">
        <p class="p_left">写博客</p>
        <div class="title_arrow"></div>
      </div>
    </div>
    <!-- 二级标题结束 -->


    <div class="blogging_page">
      <div class="center">

        <!-- 标题 -->
        <div class="blogging_header">
          <input type="text" class="blogging_title" placeholder="请输入该文章的标题" id="blogging_title">
        </div>

        <!-- 文本框 -->
        <quill-editor class="quill" v-model="content" ref="myQuillEditor" :options="editorOption" 
        @blur="onEditorBlur($event)" 
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)">
        </quill-editor>

        <!-- 博客分类 -->
        <div class="blogging_release">
          <span>博客分类：</span>
          <el-select v-model="classValue" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value"  :value="item.value">
            </el-option>
          </el-select>
        </div>

        <!-- 博客封面 -->
        <div class="blogging_release">
          <span>博客封面：</span>
          <el-upload class="avatar-uploader"
            :action="services+'/util/uploadImg/'"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>

          <input type="button" value="发布文章" class="blogging_publish" id="btn">

        </div>

      </div>
    </div>


  </div>




</template>


<script>
import { quillEditor } from "vue-quill-editor"; //调用编辑器
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import Global from '../Global'
export default {
    name: 'write-blog',
    components: {
      quillEditor
    },
    data() {
      return {
        services:Global.services,
        content: ``,
        editorOption: {},
        //分类
        options: [
          {
            value: '前端',
            // label: '前端'
          }, {
            value: 'php',
          }, {
            value: 'python',
          }, {
            value: 'hadoop',
          }, {
            value: '分布式数据库',
          }
        ],
        classValue: '',
        imageUrl: ''
      }
    },
    methods: {
      onEditorReady(editor) {},  //准备编辑器
      onEditorBlur(){}, // 失去焦点事件
      onEditorFocus(){}, // 获得焦点事件
      onEditorChange(){}, // 内容改变事件

      //博客封面
      handleAvatarSuccess(res, img) {
        this.imageUrl = URL.createObjectURL(img.raw);
        console.log(res)
      },
      beforeAvatarUpload(img) {
        const isJPG = img.type === 'image/jpeg';
        const isLt2M = img.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    },
}
</script>

<style scoped>
.quill-editor{
  background: #ffffff;
  width:80%;
  margin:30px auto;
}
.ql-video{
  display: none;
}
/* 博客封面 */
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.blogging_release{
  padding:0!important;
  width: 80%!important;
}
.blogging_release input {
  margin-left: 88px;
  margin-bottom: 20px;
}
.avatar-uploader-icon[data-v-3ebef3a1]{
  background: #fff;
  width: 217px;
  margin-bottom: 20px;
}

</style>



